<template>
  <div class="fight">
    <el-row :gutter="20">
      <el-col :span="12" v-for="i in 2" :key="i">
        <el-card>
          <div slot="header" class="clearfix">
            <span>{{i==1?'进取组':'榜样组'}}</span>
            </div>
            <div class="subArr">
          <div 
          v-for="(o,index) in (i===1?offenseArr:defenderArr)" 
          :key="o" 
          class="sub subList" 
          :class="index===(i===1?fightTemp.index.o:fightTemp.index.d)?'active':''"
          @click="setFight(i-1,o,index)">
            {{o}}
          </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <div class="tempTeam">
      <div class="vs">{{fightTemp.offense?fightTemp.offense:'等待选择'}} vs {{fightTemp.defender?fightTemp.defender:'等待选择'}}</div>
      <el-button @click="mateTeam" type="danger" plain>PK</el-button>
    </div>
  <div class="teamArr">
    <el-row :gutter="16">
      <el-col v-for="(team,index) in fightArr" :span="8" :key="index">
    <el-card  class="teamList">
      {{team.offense}} vs {{team.defender}} 
      </el-card>
      </el-col>
    </el-row>
  </div>
  </div>
</template>

<script>
export default {
  name: "fight",
  data() {
    return {
      offenseArr: ['华强小微支行', '甸柳支行', '文西支行', '历山东路支行', '山大路支行','浆水泉支行','分行营业部','开元支行','泺源大街支行'],
      defenderArr: ['二环东路支行','花园支行', '解放路支行', '朝山街支行', '燕山支行','文东支行','大明湖支行','明湖东路支行','工业南路支行'],
      fightTemp: {
        offense: null,
        defender: null,
        index: { o: null, d: null }
      },
      fightArr: new Array()
    };
  },
  methods: {
    setFight(a, o, i) {
      // a=0则为进攻方
      if (a === 0) {
        this.fightTemp.offense = o;
        this.fightTemp.index.o = i;
      } else {
        this.fightTemp.index.d = i;
        this.fightTemp.defender = o;
      }
    },
    mateTeam() {
      if (
        this.fightArr.length < 9 &&
        this.fightTemp.offense !== null &&
        this.fightTemp.defender !== null
      ) {
        let obj = JSON.parse(JSON.stringify(this.fightTemp));
        this.fightArr.push(obj);
        this.fightTemp = {
          offense: null,
          defender: null,
          index: { o: null, d: null }
        };
      } else {
        this.$alert("请确定检查双方阵容", "错误", {
          confirmButtonText: "返回"
        });
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.subArr{
  height: 300px;
  overflow: auto;
  .subList {
    padding: 0.8em;
  transition: background-color .5s;
  border-radius: 3px;
  &:hover {
    background-color: rgba(255, 0, 0, 0.336);
  }
  &.active {
    background-color: rgba(0, 110, 255, 0.336);
  }
}
}
.teamArr {
  .teamList {
    text-align: center;
    margin: 10px 0;
  }
}
.tempTeam{
  display: flex;
  flex-flow:column;
  justify-content: center;
  .vs{
    text-align: center;
    margin:20px 0;
    font-size: 2rem;
    color: #ffffff;
    text-shadow: 0 0 5px #000
  }
}
</style>



