      <!-- <transition name="el-fade-in-linear"> -->
        <!-- <el-collapse-transition> -->
          <transition name="fade">
        <keep-alive>
              <router-view/>
              <!-- </el-collapse-transition> -->
        </keep-alive>
          </transition>
      </el-main>
      <el-footer class="Appfooter">
        <router-link class="AppfooterLink" active-class="activePage" :to="{name:'HelloWorld'}">认领任务条</router-link>
        <router-link class="AppfooterLink" active-class="activePage" :to="{name:'fight'}">全年运营评价提升PK赛</router-link>
