    bottom: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.425);
    height: 30px;
    display: flex;
    justify-content: center;
    .AppfooterLink {
      padding-left: 3rem;
      padding-right: 3rem;
      display: flex;
      align-items: center;
      color: #dbdbdb;
      text-decoration: none;
      &.activePage {
        background-color: rgba(0, 0, 0, 0.575);
        line-height: 100%;
      }
    }
  }
}
</style>



