<template>
  <div class="tasks">
    <!-- 选择任务页面 -->
    <div class="elect">
      <div class="title">
        <!-- <i class="el-icon-delete"></i> -->
      </div>
      <div class="content">
        <el-row :gutter="20">
        <!-- 左 -->
            <el-col :span="18">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
            <span>支行表单</span>
            <!-- <el-button style="float: right; padding: 3px 0" type="text">清除/重选</el-button> -->
          </div>
              <div class="sub-branch">
                <el-table
                height="400px"
                :data="tableData"
                highlight-current-row
                @current-change="setTempSub"
                style="width: 100%">
                <el-table-column
                  prop="branch"
                  label="支行名称">
                </el-table-column>
                <el-table-column
                  prop="tasks[0]"
                  label="任务一">
                </el-table-column>
                <el-table-column
                  prop="tasks[1]"
                  label="任务二">
                </el-table-column>
                <el-table-column
                  prop="tasks[2]"
                  label="任务三">
                </el-table-column>
                <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="clearSubTasks(scope.$index, scope.row)">清除任务</el-button>
        </template>
      </el-table-column>
          </el-table>
              </div>
          </el-card>
          </el-col>
      <!-- 右 -->
      <el-col :span="6">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>任务名单</span>
            <!-- <el-button style="float: right; padding: 3px 0" type="text">清除/重选</el-button> -->
          </div>
          <el-checkbox-group 
            v-model="tempSub.tasks"
            :max="3"
            class="tempSubTasks"
            @change="setSubTasks"
            :min="0">
              <el-checkbox 
              style="display:block;margin:8px 0px"
              v-for="task in taskArr" 
              border 
              :label="task.name"
              size="medium" 
              :key="task.id">
              {{task.label}}
              </el-checkbox>
          </el-checkbox-group>
        </el-card>
      </el-col>
</el-row>
    </div>
  </div>
</div>
</template>

<script>
export default {
  data() {
    // 原始数据
    let name = ['二环东路支行','花园支行', '解放路支行', '朝山街支行', '燕山支行','文东支行','大明湖支行','明湖东路支行','工业南路支行','泺源大街支行','开元支行','分行营业部','浆水泉支行', '山大路支行', '历山东路支行','文西支行', '甸柳支行','华强小微支行'],
      taskLabel = ["“零”风险类差错|“零”风险类差错", "“零”违规预警|“零”违规预警", "支付、交换业务“零”差错|支付、交换业务“零”差错", "“零”屡查屡犯问题|“零”屡查屡犯问题", "监管红线“零”容忍|监管红线“零”容忍", "服务排名全行前 30 |服务排名全行前 30 ", "“零”反洗钱差错|“零”反洗钱差错", "集中授权差错≤2 |集中授权差错≤2 ", "账户、国库业务“零”差错|账户、国库业务“零”差错"],
      tableData = new Array(),
      taskArr = new Array();
    // 支行构造函数
    function Bank(name) {
      this.branch = name;
      this.tasks = new Array();
    }
    // 任务构造函数
    function Task(label, key) {
      this.label = label.split("|")[1];
      this.name = label.split("|")[0];
      this.id = key;
    }
    // 循环生成所有银行对象
    for (let key in name) {
      tableData.push(new Bank(name[key]));
    }
    // 生成所有任务对象
    for (let key in taskLabel) {
      taskArr.push(new Task(taskLabel[key], key));
    }

    return {
      tableData,
      // 任务列表
      taskArr,
      // 选择的支行
      tempSub: tableData[0]
    };
  },
  methods: {
    // 更改当前银行
    setTempSub(a) {
      this.tempSub = a;
    },
    // 更改绑定的任务
    setSubTasks(a) {
      this.tempSub.tasks = a;
    },
    clearSubTasks(a, b, c) {
      b.tasks.splice(0, b.tasks.length);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.item {
  margin-bottom: 18px;
}
.tempSubTasks{
  height: 400px;
  overflow:auto;
}
</style>



